import React from 'react';
import Card from './Card';

export default ( {list_data, showProfile} ) => {

		
    return (
		
      <div className="cards-list">
			<ul>
				{
					list_data.map( item => 
						<li key={item.id}
						onClick={()=> showProfile(item) } 
						className="card-item"> 
						<Card {...item} /> </li> 
						)
				}
			</ul>
	  </div>
	  
    )
}



