import React, { Component } from 'react';
import fetch from 'isomorphic-fetch';
import Card from './Card'; 
import TopBar from './TopBar'; 
import List from './List'; 
import Filter from './Filter'; 
import Profile from './Profile'; 

export default class Browse extends Component {
    constructor(props){
        super(props);
        this.state = {
            original_list:[],
            displayed_list:[],
            displayed_profile:{}
        }
    }
    componentWillMount(){
		fetch('//localhost:3000/static/data/MOCK_DATA.json')
            .then( response => {
                if(response.ok) {
                    return response.json();
                }
                throw new Error('Network response was not ok.');
            })
            .then( data => {
                 //console.table(data);
                // console.log(`${data.length} items loaded`);
                 this.setState({
                     original_list:data,
                     displayed_list: data,
                     displayed_profile: data[0]
                });
            })
            .catch( error => {
                console.error(`fetch operation failed: ${error.message}`);
            });
	}
    update_list_state(filtered_list){
       // console.log('update_list_state',filtered_list.length)
        this.setState({
            original_list: this.state.original_list,
            displayed_list: filtered_list
        })
   }
       showProfile( robotProfile ){
       // console.log('robotProfile',robotProfile);
         this.setState({
           displayed_profile: robotProfile
         });
    }
    render() {
        return (
        <div className="app">
            <div className="header">
                 <h1 className="headline">Browse our bots</h1>
                    <div className="filter-box">
                <h2 className="filter_title">{this.state.displayed_list.length} items filtered:</h2>
                <Filter className="input-filter" 
                        list_data={this.state.original_list} 
                        onfilter={(fl)=> this.update_list_state(fl)}/>
            </div>
            </div>
             <div className="content-box">
             <Profile robot_info={this.state.displayed_profile}/>
            <List list_data={this.state.displayed_list} showProfile={(item) => this.showProfile(item)}/>
                </div>
        </div>
        )
    }
}


