import React, { Component, PropTypes} from 'react';

export default class Filter extends Component {
    constructor(props) {
        super(props);
    }
    filter_list(e) {
        let txt = e.target.value;
        const {list_data, onfilter} = this.props;
        let filtered_list = list_data.filter((item) => {
            return item.first_name.toLowerCase().includes(txt.toLowerCase())
        })
        onfilter(filtered_list);
    }
    render() {
        return (
            <div>
                <input type="text" className="input-filter" onChange={(e) => this.filter_list(e)} />
            </div>
        )
    }
}
Filter.propTypes = {
   onfilter: PropTypes.func.isRequired,
   list_data: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.number.isRequired,
    first_name: PropTypes.string.isRequired,
      last_name: PropTypes.string.isRequired,
    email: PropTypes.string.isRequired,
    country: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
  avatar: PropTypes.string.isRequired,
   })).isRequired
}





