import React from 'react';
import TopBar from './TopBar'; 
import Browse from './Browse'; 
import Welcome from './Welcome'; 
import Create from './Create'; 
import {
    BrowserRouter,
    HashRouter,
    Route,
    Link,
    Switch
} from 'react-router-dom';

// const Home = () => <h3 className="headline-text">Home</h3>;
// const Create = () => <h3 className="headline-text">Create</h3>;

const Links = () => (
    <ul className="link-bar">
        <li><Link to="/" className="nav-link">Home</Link></li>
        <li><Link to="/browse" className="nav-link">Browse</Link></li>
        <li><Link to="/create" className="nav-link">Create</Link></li>
    </ul>
)
const App = ()=> (
        <HashRouter>   
            <div className="app-router">
                <TopBar></TopBar>
                <div className="top-bar-links"><Links /></div>
                <Route exact path="/" component={Welcome} />
                <Route path="/browse" component={Browse}></Route>
		        <Route path="/create" component={Create}></Route>
            </div>
        </HashRouter>  
)

export default App


