export default class Profile extends Component {
    constructor(props) {

        super(props);
    }

    render() {
        //  console.log('this.props.robot_info',this.props.robot_info);
        //  //console.log('robot_info',robot_info);
        //  console.log('this.props',this.props);
       
        return (
            <div className="profile-holder">
                <div className="profile-image-holder">
                    <img className="avatar" src={this.props.robot_info.avatar} />
                </div>
                <div className="profile-details-box">
                    <h2 className="profile-name"> {this.props.robot_info.first_name}</h2>
                    <h2 className="profile-name"> {this.props.robot_info.last_name}</h2>
                    <span className="grad-line"/>
                    <h5 className="profile-details"> {this.props.robot_info.id}</h5>
                    <h5 className="profile-details"> {this.props.robot_info.country}</h5>
                    <h5 className="profile-details"> {this.props.robot_info.email}</h5>
                    <h5 className="profile-details"> {this.props.robot_info.description}</h5>
                </div>
            </div>
        )
    }
}


